.wp-block-outermost-social-sharing {
  align-items: flex-start;
  padding-left: 0;
  padding-right: 0;
  text-indent: 0;
  margin-left: 0;
  background: none;
}
.wp-block-outermost-social-sharing .outermost-social-sharing-link a,
.wp-block-outermost-social-sharing .outermost-social-sharing-link a:hover {
  text-decoration: none;
  border-bottom: 0;
  box-shadow: none;
}
.wp-block-outermost-social-sharing .outermost-social-sharing-link a {
  padding: 0.25em;
}
.wp-block-outermost-social-sharing .outermost-social-sharing-link svg {
  width: 1em;
  height: 1em;
}
.wp-block-outermost-social-sharing .outermost-social-sharing-link span:not(.screen-reader-text) {
  margin-left: 0.5em;
  margin-right: 0.5em;
  font-size: 0.65em;
}
.wp-block-outermost-social-sharing.has-small-icon-size {
  font-size: 16px;
}
.wp-block-outermost-social-sharing, .wp-block-outermost-social-sharing.has-normal-icon-size {
  font-size: 24px;
}
.wp-block-outermost-social-sharing.has-large-icon-size {
  font-size: 36px;
}
.wp-block-outermost-social-sharing.has-huge-icon-size {
  font-size: 48px;
}
.wp-block-outermost-social-sharing.aligncenter {
  justify-content: center;
  display: flex;
}
.wp-block-outermost-social-sharing.alignright {
  justify-content: flex-end;
}

.wp-block-outermost-social-sharing-link {
  display: block;
  border-radius: 9999px;
  transition: transform 0.1s ease;
  height: auto;
}
.wp-block-outermost-social-sharing-link a {
  align-items: center;
  display: flex;
  line-height: 0;
  transition: transform 0.1s ease;
}
.wp-block-outermost-social-sharing-link:hover {
  transform: scale(1.1);
}

.wp-block-outermost-social-sharing .wp-block-outermost-social-sharing-link .wp-block-outermost-social-sharing-link-anchor, .wp-block-outermost-social-sharing .wp-block-outermost-social-sharing-link .wp-block-outermost-social-sharing-link-anchor:hover, .wp-block-outermost-social-sharing .wp-block-outermost-social-sharing-link .wp-block-outermost-social-sharing-link-anchor:active, .wp-block-outermost-social-sharing .wp-block-outermost-social-sharing-link .wp-block-outermost-social-sharing-link-anchor:visited,
.wp-block-outermost-social-sharing .wp-block-outermost-social-sharing-link .wp-block-outermost-social-sharing-link-anchor svg {
